.popup__container {
    background-color: white;
    margin: 0;
    padding: 0;
    max-width: 430px;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    position: relative;
}

@media screen and (max-width: 420px) {
    .popup__container {
        width: 282px;
        top: 123px;
    }
}