@import url(./__copyright/footer__copyright.css);
.footer {
    margin: 67px 0 60px 0;
    padding: 0;
}

@media screen and (max-width: 480px) {
    .footer {
        width: calc(100% - 38px);
        margin-left: auto;
        margin-right: auto;
    }
}

@media screen and (max-width: 320px) {
    .footer {
        margin: 50px 0 36px 19px;
    }
}